import {idiom as lang } from 'entcore/entcore';

/**
 * Retourne la valeur de la cl i18n
 * @param key cl i18n
 * @returns {SVGMatrix|MSCSSMatrix|any|WebKitCSSMatrix|void} Valeur de la cl i18n
 */
export function translate(key){
    return lang.translate(key)
};


