/**
 * Recherche true si une chaine de caractre contient un mot clef.
 * La casse est ignore et la recherche d'un mot clef vide ou contenant que des espaces
 * retourne false
 * @param psString la chaine de caractre
 * @param psKeyword le mot clef recherch
 * @returns {boolean} true si a match false sinon
 */
export function containsIgnoreCase(psString, psKeyword){
    if (psKeyword !== undefined) {
        return psString.toLowerCase().indexOf(psKeyword.toLowerCase())>=0 && psKeyword.trim() !== "";
    } else {
        return false;
    }
};


